@import url(https://fonts.googleapis.com/css?family=Poppins);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', Arial, Helvetica, sans-serif;
  line-height: 1.7;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  padding: 0;

}

.btn {
  display: inline-block;
  border: none;
  background: #00cc33;
  color: #fff;
  padding: 10px 20px;
  cursor: pointer;  
}

.btn:hover {
  background: #00b42d;
}

input {
  padding: 1 rem;
}
